@media only screen and (max-width: 1416px) {
  .favorite-box {
    width: 90%;
  }
  .about-box {
    width: 90%;
  }
  .about-box .row-one .image-box img {
    width: 100%;
  }
  .projects {
    width: 100%;
  }
  #contact .container {
    width: 100%;
  }
  .contact-box {
    width: 90%;
  }
  .info-box {
    width: 90%;
  }
}
@media only screen and (max-width: 1131px) {
  .favorite-item {
    width: 220px;
    height: 40px;
  }
}
@media only screen and (max-width: 906px) {
  .favorite-item p {
    font-size: 15px;
  }
  .about-box {
    flex-direction: column;
    text-align: center;
    height: 640px;
    align-items: center;
    width: 90% !important;
    padding: 30px !important;
  }
  .about-box .row-one .image-box img {
    border-radius: 20px;
    width: 250px;
  }
  .intro {
    width: 70%;
    margin: auto;
  }
  .about-box .row-one {
    width: 100%;
  }
  .about-box .row-two {
    width: 100%;
  }
  .row-two button {
    margin: 20px auto 0 auto;
  }
  .about-box .row-one .image-box {
    width: 250px;
    height: 250px;
    display: flex;
    justify-content: center;
    margin: auto;
  }
  .skillbox {
    width: 100%;
  }
  .categories {
    width: 90%;
  }
  .categories p {
    font-size: 18px;
  }
  .socials-box {
    margin: 0 15px !important;
  }
  .socials-box h2 {
    font-size: 12px !important;
  }
  #contact .container {
    width: 100%;
  }
  .contact-box {
    width: 94%;
  }
  .first-side {
    flex-direction: column !important;
  }
  .first-side input {
    margin: 5px 0 !important;
    font-size: 15px !important;
  }
  .contact-box textarea {
    font-size: 17px !important;
  }
}
@media only screen and (max-width: 600px) {
  #portfolio,
  #About-me,
  #contact,
  #resume {
    width: 100%;
  }
  .favorite-item p {
    font-size: 18px;
  }
  .favorite-item {
    margin: 10px 0;
  }
  .favorite-item {
    width: 80%;
    height: 50px;
  }
  .favorite-box {
    flex-direction: column;
    height: 250px;
  }
  .about-box {
    width: 100% !important;
    height: 600px;
  }
  .intro {
    width: 80%;
    font-size: 13px !important;
    margin: auto;
  }
  .about-title h1,
  .favorite-title h1 {
    font-size: 30px;
  }
  .bar-box p {
    font-size: 15px;
  }
  .bar-box .bar {
    height: 6px;
  }
  .bar-box .bar .inside-bar::after {
    width: 13px;
    height: 13px;
    top: -6px;
  }
  .resume-title-box h1 {
    font-size: 20px;
  }
  .project-title-box h1 {
    font-size: 30px;
  }
  .projects {
    margin-top: 30px;
    width: 100%;
  }
  .project {
    width: 40%;
    height: 250px;
  }
  .categories {
    width: 100%;
  }
  .categories p {
    font-size: 15px;
    text-align: center;
  }
  .info-box {
    width: 90%;
    padding: 0px !important;
  }
  .socials-box h2 {
    font-size: 10px !important;
  }
  .first-side input {
    margin: 5px 0 !important;
    font-size: 13px !important;
  }
  .contact-box textarea {
    font-size: 15px !important;
  }
  .contact-title-box h1 {
    font-size: 30px;
  }
}
@media only screen and (max-width: 420px) {
  .about-box .row-one .image-box img {
    width: 200px;
    height: 200px;
  }
  .about-box .row-one .image-box {
    height: 200px;
  }
  .intro {
    width: 100%;
    font-size: 13px !important;
    margin: auto;
  }
  .info-box {
    width: 90%;
    padding: 20px !important;
    flex-direction: column;
    height: 500px;
  }
  .socials-box h2 {
    font-size: 12px !important;
  }
}
