@import url(../index.css);
@import url(../azarmehr.css);
#portfolio,
#About-me,
#contact,
#resume {
  width: calc(100% - 90px);
  height: 100%;
  overflow: hidden;
  background-color: #0e0f10;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 11;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  font-size: 20px;
  color: white;
}
#portfolio {
  overflow-y: scroll;
}
#portfolio::-webkit-scrollbar {
  width: 0.5vw;
}
#portfolio::-webkit-scrollbar-thumb {
  background-color: #25ca7f;
  height: 2px;
  width: 100%;
  border-radius: 30px;
}
.badge {
  width: 90px;
  height: 2px;
  background-color: #25ca7f;
  position: relative;
  margin-top: 10px;
}
.badge::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: -8px;
  right: 20px;
  background-color: #0e0f10;
  border: 2px solid #25ca7f;
  -webkit-border-radius: 50px 50px 50px 0;
  border-radius: 50px 50px 50px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.badge::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: -8px;
  left: 20px;
  background-color: #0e0f10;
  border: 2px solid #25ca7f;
  -webkit-border-radius: 50px 50px 50px 0;
  border-radius: 50px 50px 50px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(222deg);
}

.container {
  width: 90%;
  height: 100%;
}
#About-me {
  overflow-y: scroll;
}
#About-me::-webkit-scrollbar {
  width: 0.5vw;
}
#About-me::-webkit-scrollbar-thumb {
  background-color: #25ca7f;

  height: 2px;
  width: 100%;
  border-radius: 30px;
}
#About-me::-webkit-scrollbar-track {
  background-color: #070708;
}
.about-title {
  width: 100%;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  font-family: "Avini";
  margin-top: 60px;
}
.about-title h1 {
  font-family: "avini";
  font-size: 38px;
}

.about-box {
  background-color: #070708;
  width: 1100px;
  height: 400px;
  margin-top: 40px;
  margin: 50px auto 0 auto;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  padding: 20px;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}
.about-box:hover {
  transform: translateY(-5px);
}
.about-box .row-one {
  width: 30%;

  display: flex;
  justify-content: right;
}
.about-box .row-one .image-box {
  margin-top: 30px;
  width: 280px;
  margin-right: 20px;
  height: 280px;
}
.about-box .row-one .image-box img {
  width: 280px;
  border-radius: 50%;
}
.about-box .row-two {
  width: 70%;
  justify-content: center;
  height: 90%;
  display: flex;
  direction: rtl;
  padding: 20px;
  flex-direction: column;
}
.row-two button {
  background: transparent;

  color: #25ca7f;
  font-family: "primary-font";
  font-size: 17px;

  cursor: pointer;
  border-top: none;
  width: 140px;
  height: 50px;
  border-bottom: 2px solid #25ca7f;
  border-left: none;
  border-right: none;
  transition: 0.3s;
  margin-top: 20px;
}
.row-two button:hover {
  background: #25ca7f;
  color: #0e0f10;
  border-radius: 5px;
}
.about-box .row-two h3 {
  font-family: "avini";
}
.about-box .row-two .loc {
  color: #25ca7f;
  font-size: 15px;
  font-family: "primary-font";
}
.about-box .row-two .intro {
  margin-top: 10px;
  font-family: "primary-font";
  font-size: 15px;
}
.favorite-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 30px;
}

.favorite-box {
  width: 1100px;
  background-color: #070708;
  height: 100px;
  border-radius: 20px;
  margin: 50px auto 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.favorite-box:hover {
  transform: translateY(-6px);
}
.favorite-item {
  background-color: #0e0f10;
  margin: 0 20px;
  display: flex;
  flex-direction: row-reverse;
  width: 250px;
  height: 50px;
  border-radius: 20px;
  align-items: center;
  padding: 10px;
  transition: 0.3s;
}
.favorite-item svg {
  color: #25ca7f;
}
.favorite-item p {
  margin-right: 40px;
  position: relative;
  font-size: 18px;
}
.favorite-item p::after {
  content: "";
  width: 4px;
  height: 60px;
  background-color: #070708;
  position: absolute;
  right: -20px;
  top: -16px;
}
.favorite-item:hover {
  background: transparent;
}
/*-------------------------------------------------------------------------*/

#resume::-webkit-scrollbar {
  width: 0.5vw;
}
#resume::-webkit-scrollbar-thumb {
  background-color: #25ca7f;

  height: 2px;
  width: 100%;
  border-radius: 30px;
}
#resume::-webkit-scrollbar-track {
  background-color: #070708;
}
#resume {
  overflow-y: scroll;
}
.resume-title-box {
  margin-top: 30px;
  display: flex;

  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.resume-title-box h1 {
  width: 100%;
  display: flex;
  font-size: 29px;
  margin-bottom: 10px;
  align-items: center;
  justify-content: center;
  font-family: "avini";
}
.skillbox {
  width: 800px;
  height: 400px;
  background-color: #070708;
  border-radius: 20px;
  margin: 40px auto 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  transition: 0.3s;
}
.skillbox:hover {
  transform: translateY(-7px);
}
.bar-box {
  margin-top: 20px;
  display: flex;

  text-align: right;
  direction: rtl;
  flex-direction: column;
}
.bar-box p {
  font-family: "primary-font";
  font-size: 17px;
  margin-right: 40px;
  margin-bottom: 5px;
}
.bar-box .bar {
  margin: auto;
  width: 90%;
  height: 10px;
  border-radius: 10px;
  background-color: #0e0f10;
}
.bar-box .bar .inside-bar {
  background-color: #25ca7f;
  height: 100%;
  position: relative;
  border-radius: 20px;
}
.bar-box .bar .inside-bar::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #070708;
  border-radius: 50%;
  left: 0;
  top: -8px;
  border: 2px solid #25ca7f;
}
/*-------------------------------------------------------------------------*/
.project-title-box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
}
.project-title-box h1 {
  letter-spacing: -3px;
  font-family: "avini";
}
.categories {
  width: 500px;

  height: 60px;
  margin: 30px auto 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.categories p {
  font-family: "primary-font";
  cursor: pointer;
  transition: 0.3s;

  width: 110px;
  text-align: center;
}
.categories p.active {
  color: #25ca7f;
  transform: scale(120%);
}
.projects {
  width: 80%;
  height: 400px;
  margin: 90px auto 0 auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.project {
  width: 230px;
  height: 400px;
  margin: 20px 10px;
  background-color: #070708;
  border-radius: 10px;
  cursor: pointer;
}
.project .cover {
  color: black;
  font-weight: 600;
  font-family: "primary-font";
  font-size: 27px;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  color: white;
  opacity: 0;
  transition: 0.3s;
  background: radial-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
}
.project:hover .cover {
  opacity: 1;
}
/*-------------------------------------------------------------------------*/
.contact-title-box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
}
.contact-title-box h1 {
  font-family: "avini";
}
.contact-box {
  transition: 0.3s;
  width: 1200px;
  height: 550px;
  background: #070708;
  margin: 40px auto 0 auto;
  border-radius: 10px;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.contact-box:hover {
  transform: translateY(-7px);
}
.contact-box .first-side {
  display: flex;
  width: 100%;
  flex-direction: row-reverse;
}
.contact-box .first-side input {
  direction: rtl;
  padding: 20px;
  width: 100%;
  margin: 0 10px;
  background: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  border-bottom: 1px solid rgb(95, 95, 95);
  font-family: "primary-font";
  font-size: 16px;
  color: rgb(95, 95, 95);
  height: 30px;
}
.contact-box .first-side input:focus {
  border-bottom: 1px solid #25ca7f;
}
#contact {
  overflow-y: scroll;
}
#contact::-webkit-scrollbar {
  width: 0.5vw;
}
#contact::-webkit-scrollbar-thumb {
  background-color: #25ca7f;

  height: 2px;
  width: 100%;
  border-radius: 30px;
}
#contact::-webkit-scrollbar-track {
  background-color: #070708;
}
.contact-box textarea {
  font-family: "primary-font";
  color: rgb(95, 95, 95);
  font-size: 16px;
  direction: rtl;
  resize: none;
  background: transparent;
  margin-top: 20px;
  width: 100%;
  height: 400px;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  border-bottom: 1px solid rgb(95, 95, 95);
  padding: 20px;
}
.contact-box textarea:focus {
  border-bottom: 1px solid #25ca7f;
}
.contact-box button {
  background: transparent;
  border-bottom: 2px solid #25ca7f;

  color: #25ca7f;
  font-family: "primary-font";
  font-size: 17px;
  padding: 12px 50px;
  cursor: pointer;
  border-top: none;
  border-left: none;
  border-right: none;
  transition: 0.3s;
  margin-top: 20px;
}
.contact-box button:hover {
  background: #25ca7f;
  color: #0e0f10;
  border-radius: 5px;
}
.info-box {
  background: #070708;
  width: 1200px;
  height: 250px;
  margin: 50px auto 0 auto;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.info-box:hover {
  transform: translateY(-7px);
}
.info-box .socials-box {
  width: 200px;
  height: 100%;
  margin: 0 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.info-box .socials-box svg {
  margin-bottom: 30px;
}
.info-box .socials-box h2 {
  font-size: 14px;
  font-family: "primary-font";
  color: rgb(218, 218, 218);
}
.info-box .socials-box p {
  font-family: "primary-font";
  color: rgb(95, 95, 95);
  font-size: 13px;
  margin-top: 10px;
}
