@font-face {
  font-family: "avini";
  src: url(../Fonts/aviny-700.eot),
    url(../Fonts/aviny-700.woff2) format("woff2"),
    url(../Fonts/aviny-700.woff) format("woff"),
    url(../Fonts/aviny-700.ttf) format("truetype");
  font-weight: 700;
}
html {
  overflow: hidden;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "vazir";
}
.loading {
  background: #0e0e0e;
  width: 100%;
  height: 100%;
  z-index: 10000;
  position: absolute;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.body {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(./Images/bg.jpg);
  background-position: center;
  background-size: cover;
}

.navbar {
  width: 90px;

  transition: 0.3s;
  height: 100%;
  position: absolute;
  top: 50%;
  z-index: 100;
  transform: translateY(-50%);
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
}

.navbar1 {
  display: none;
}
.navbar2 {
  display: block;
}
.logo img {
  width: 50px;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}
.list-item {
  position: relative;
  margin-top: 35px;
  cursor: pointer;

  height: 40px;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.item-logo {
  color: #a7afbb;
  transition: 0.2s;
}
.list-item:hover .item-logo {
  transform: scale(120%);
  color: rgb(255, 255, 255);
}

.list-item .item-badge {
  position: absolute;
  left: 90px;
  text-align: center;
  padding: 10px 20px;
  min-width: 100px;
  border-radius: 10px;
  font-size: 15px;
  background-color: #0e0e0e;
  color: #d1d6df;
  opacity: 0;
  z-index: 10;
  visibility: none;
  transition: 0.14s ease-in-out;
}
.list-item:hover .item-badge {
  visibility: visible;
  opacity: 0.9;

  left: 70px;
}
.player-handle {
  width: 50px;
  height: 50px;

  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: 0.3s;
}
.player {
  width: 50px;
  height: 50px;

  display: flex;
  justify-content: center;
  transition: 0.3s;
  align-items: center;
  position: absolute;

  left: 50%;
  transform: translateX(-50%);
}
.player span {
  width: 3px;
  height: 5px;
  background-color: aliceblue;
  margin: 0 2px;
}
.player :nth-child(1) {
  animation: wave 0.8s 0.5s ease-out alternate infinite;
}
.player :nth-child(2) {
  animation: wave 0.8s 0.7s ease-out alternate infinite;
}
.player :nth-child(3) {
  animation: wave 0.8s 0.25s ease-out alternate infinite;
}
.player :nth-child(4) {
  animation: wave 0.8s 0.5s ease-out alternate infinite;
}
.player :nth-child(5) {
  animation: wave 0.8s 0.6s ease-out alternate infinite;
}
@keyframes wave {
  0% {
    height: 5px;
  }
  50% {
    height: 30px;
  }
  100% {
    height: 5px;
  }
}
.social-media {
  position: absolute;
  right: 20px;
  bottom: 30px;
}
.social-media ul {
  list-style: none;
}
.social-media ul li {
  margin-top: 30px;
}
.social-media ul li a {
  border-radius: 10px;
  padding: 10px;
  padding: 8px;
  color: white;
  text-decoration: none;
}

.social-media ul li:hover .social-link {
  color: rgb(17, 16, 16);
  background-color: white;
  border-radius: 10px;
  padding: 8px;
}
.name {
  width: 90%;
  flex-direction: column;
  margin: auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "primary-font";
}
.name h3 {
  font-family: "vazir";
  color: #d1d6df;
  background-color: rgb(27, 27, 27);
  padding: 10px 18px;
  border-radius: 20px 20px 20px 2px;
  transform: translateY(10px);
  margin-left: 300px;
  z-index: 10;
}
.name h1 {
  font-family: "avini";
  z-index: 10;
  color: #d1d6df;

  font-size: 50px;
}
.hamburger {
  width: 0px;
  transition: 0.3s;
  overflow: hidden;
  height: 50px;
  position: absolute;
  right: 20px;
  display: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 10px;
  z-index: 1000;
}
.hamburger svg {
  color: white;
}
