@media only screen and (max-width: 600px) {
  .navbar {
    backdrop-filter: blur(5px);
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
    width: 50px;
    overflow: visible;
  }
  .item-badge {
    display: none;
  }
  .hamburger {
    width: 50px !important;

    overflow: hidden;
  }
  .navbar {
    display: block !important;
  }
  .navbar2 {
    display: none !important;
  }
  .name {
    width: 100%;
  }
  .name h3 {
    margin-right: 20px;
    font-size: 18px;
  }
  .name h1 {
    margin-right: 20px;
    font-size: 40px !important;
  }
}
@media only screen and (max-width: 500px) {
  .name h3 {
    width: 120px;
    text-align: center;
    margin-right: 80px;
    font-size: 15px;
  }
  .name h1 {
    margin-right: 20px;
    font-size: 40px !important;
  }
}
